import React, { useEffect } from 'react'
import './style.css'
import { useParams } from 'react-router-dom';
import * as logo from '../../../assets/Clean Walk/Loading_Screen/Search.logo.small.jpg.png'
import { instancestore } from '../../../store/InstanceStore';

const Spinner = () => {

  const { instance, loadinstance } = instancestore();
  let params = useParams();

  useEffect(() => {

    if (!instance) {
      loadinstance()
    }

  }, [])

  const loadinstanceicon = (instanceid: any) => {
    let a = " ";
    (instance) && instance.map((obj: any) => {
      // console.log(">> title >> : ", title, obj.l_title);

      if (String(instanceid) === String(obj.id)) {
        // console.log(">> obj.l_icon >> : ", obj.l_icon);
        a = obj.mascot
      } else { null }
    })
    return a
  }

  return (
    <div  className="maian_container">

      <div className='loader_logoicon_containers'>
        <img className='loader_logoicon' src={loadinstanceicon(params.instanceId)} />
        <div className="loader">
        </div>

      </div>

      <div className="poweredby_text">
        Powered by Media Cannibal.
      </div>

    </div>
  )
}

export default Spinner